<!-- 系统管理 -->
<template>
	<div class="container" >
		<div class="tree-aside">
			<div class="current">{{$t('menuManage.curMenuTitle')}}</div>
			<el-row class="row row-center">
				<click-button-add class="button" :listLi="listLi" :title="$t('menuManage.addTitle')" :rules="rules" :formLabelWidth="formLabelWidth" :addDialog="addDialog" @addConfirm="addConfirm" @addInfoCancel="menuAddInfoCancel" @clickAddInfo="menuClickAddInfo"></click-button-add>
				<click-button-revise @reviseInfoCancel="reviseMenuCancel" @reviseConfirm="reviseConfirm" @clickReviseInfo="reviseMenuInfo" class="button" :formLabelWidth="formLabelWidth" :listLi="listLi" :title="$t('menuManage.reviseTitle')" :rules="rules" :info="curNode" :reviseDialog="reviseDialog" :menuParentId="menuParentId"></click-button-revise>
				<el-button type="primary" @click="clickDelete" class="button" size="small" icon="el-icon-close">{{$t('button.delete')}}</el-button>
			</el-row>
			<el-aside class="el-aside" width="250">
				<el-tree class="border-blue" @node-expand="nodeExpand" :expand-on-click-node="false" @node-collapse="nodeCollapse" empty-text default-expand-all :data="menuTree" @node-click="clickNode" node-key="id" highlight-current ref="menuTree" :props="defaultProps">
					<span class="custom-tree-node" slot-scope="{ node, data }">
						<span v-if="data.child==undefined || data.child.length == 0" style="color:#00a8d4;font-size:14px;" class="icon iconfont">&#xe606;</span>
						<span v-if="data.child!=undefined && data.child.length != 0" style="color:#00a8d4;font-size:12px;" class="icon iconfont">&#xe602;</span>
						<span v-if="data.type=='node' && !data.opend" style="color:#00a8d4;font-size:14px;" class="icon iconfont">&#xe642;</span>
						<span>{{ node.label }}</span>
					</span>
				</el-tree>
			</el-aside>
		</div>
		<el-container class="content">
			<div class="current">{{$t('menuManage.curModelTitle')}}</div>
			<el-row class="row">
				<click-button-add class="button" :menuId="curNode.id" :menuParentId="menuParentId" :listLi="modelListLi" :title="$t('menuManage.addModelTitle')" :rules="rulesModel" :formLabelWidth="formLabelWidth" :addDialog="addModelDialog" @addConfirm="addModelConfirm"  @addInfoCancel="modelAddInfoCancel" @clickAddInfo="modelClickAddInfo" ></click-button-add>
				<click-button-revise class="button" :listLi="modelListLi" :title="$t('menuManage.reviseModelTitle')" :rules="rulesModel" :reviseDialog="reviseModelDialog" :formLabelWidth="formLabelWidth" @reviseInfoCancel="reviseModelCancel" @reviseConfirm="reviseModelConfirm" @clickReviseInfo="reviseModelInfo" ></click-button-revise>
				<el-button type="primary" @click="clickModelDelete" class="button" size="small" icon="el-icon-close">{{$t('button.delete')}}</el-button>
				<el-row class="model-input">
					<span class="name">{{$t('menuManage.modelListLi.moduleUniqeCode.label')}}</span>
					<el-input  class="input" size="small" v-model="search.modelNum"></el-input>
				</el-row>
				<el-row class="model-input">
					<span class="name">{{$t('menuManage.modelListLi.moduleName.label')}}</span>
					<el-input class="input" size="small" v-model="search.modelName"></el-input>
				</el-row>
				<el-button type="primary" @click="query" class="button" size="small" icon="el-icon-search">{{$t('button.search')}}</el-button>
			</el-row>
			<div ref="tableBox" class="table-box">
				<el-table ref="table" @select="select" @select-all="selectAll" @row-click="rowClick" :data="tableData" :height="tableH" class="center" size="mini" style="text-align:center;width:100%;">
					<el-table-column width="100" type="selection" height="40px"></el-table-column>
					<el-table-column v-for="(v,index) in $t('menuManage.tableTitle')" :prop="v.prop" :width="v.width" :label="v.label" :key="index"></el-table-column>
				</el-table>
				<div ref="pagination" class="block">
					<el-pagination class="pagination" :page-size="pageSize" @current-change="changeCurPage" layout="prev, pager, next" :total="total"></el-pagination>
				</div>
			</div>
		</el-container>
	</div>
</template>

<script>
const clickButtonAdd = ()=> import("../../../../components/clickButtonAdd");
const clickButtonRevise = ()=> import("../../../../components/clickButtonRevise");
var ParentFn = ()=>import('../../../../conmon/js/commo.js');
var Parent;
import store from '@/store'
export default {
	name: 'menuManage',
	components:{
		clickButtonAdd,
		clickButtonRevise
	},
	data() {
		return {
			search: {},
			menuTree: [],
			defaultProps: {
				children: 'child',
				label: 'menuName'
			},
			tableData: [],
			tableDataAll: [],
			total: 0,
			tableH: 0,
			pageSize: 20,
			menuParentId: '00000000-0000-0000-0000-000000000000',
			listLi: {
				menuCode: {
					label: '菜单编码：',
					type: 'input',
					model: ''
				},
				menuName: {
					label: '菜单名称：',
					type: 'input',
					model: ''
				},
				menuEnName: {
					label: '菜单英文名称：',
					type: 'input',
					model: ''
				},
				menuIcon: {
					label: '菜单图标：',
					type: 'input',
					model: ''
				},
				menuType: {
					label: '菜单类型：',
					type: 'dropDown',
					model: 0,
					children: ['单页面','选项卡']
				},
				menuIsHomePage: {
					label: '是否首页：',
					type: 'dropDown',
					model: 0,
					children: ['否','是']
				},
				sortNum: {
					label: '排序码：',
					type: 'inputNumber',
					model: ''
				},
				memo: {
					label: '备注：',
					type: 'textarea',
					model: ''
				}
			},
			modelListLi:{
				moduleName: {
					label: '模块名称：',
					type: 'input',
					model: ''
				},
				moduleUniqeCode: {
					label: '唯一编码：',
					type: 'input',
					model: ''
				},
				moduleEnName: {
					label: '英文模块名称：',
					type: 'input',
					model: ''
				},
				moduleIcon: {
					label: '模块图标：',
					type: 'input',
					model: ''
				},
				moduleVersion: {
					label: '模块版本：',
					type: 'input',
					model: ''
				},
				moduleEntrance: {
					label: '模块入口：',
					type: 'input',
					model: ''
				},
				moduleDscr: {
					label: '模块描述：',
					type: 'input',
					model: ''
				},
				sortNum: {
					label: '排序码：',
					type: 'inputNumber',
					model: ''
				},
				memo: {
					label: '备注：',
					type: 'textarea',
					model: ''
				}
			},
			curNode: {},
			title: '',
			addTitle: '菜单添加',
			reviseTitle: '修改菜单',
			rules: {},
			formLabelWidth: '120px',
			deleteWarning: '确定要删除此条记录？',
			deleteUrl: '',
			deleteMsg: '请选择一条记录！',
			deleteInfo: {},
			searchUrl: '',
			addDialog: {
				addDialog: false
			},
			reviseDialog: {
				reviseDialog: false
			},
			addModelTitle: '模块添加',
			rulesModel: {},
			reveseModelTitle: '修改模块',
			modelInfo: {},
			addModelDialog: {
				addDialog: false
			},
			reviseModelDialog: {
				reviseDialog: false
			}
		}
	},
	created() {
		var that = this;
		ParentFn().then(value =>{
			Parent = new value.default.ParentFn();
			//把中英文的参数赋值给Parent
			Parent.error = this.$t('error');
			Parent.success = this.$t('success');
			Parent.button = this.$t('button');
			Parent.message = this.$t('message');
			that.listLi = this.$t('menuManage.listLi');
			that.modelListLi = this.$t('menuManage.modelListLi');
			this.rules = {
				menuCode: [
				{validator: Parent.noIsEmpty(this.$t('menuManage.menuRules.menuCode')),required: true, trigger: 'blur' }
				],
				menuName: [
				{validator: Parent.noIsEmpty(this.$t('menuManage.menuRules.menuName')), required: true, trigger: 'blur' }
				]
			};
			this.rulesModel = {
				moduleName: [
				{validator: Parent.noIsEmpty(this.$t('menuManage.modelRules.moduleName')),required: true, trigger: 'blur' }
				],
				moduleUniqeCode: [
				{validator: Parent.noIsEmpty(this.$t('menuManage.modelRules.moduleUniqeCode')), required: true, trigger: 'blur' }
				],
				moduleEntrance: [
				{validator: Parent.noIsEmpty(this.$t('menuManage.modelRules.moduleEntrance')), required: true, trigger: 'blur' }
				]
			}
			//获取菜单
			Parent.ajax(
				'api/services/app/SmMenu/QueryAll',
				{
					"id": that.menuParentId
				},
				that,
				(res)=>{
					if(res.success) {
						var list = res.result.items;
						console.log(list,typeof list)
						if(res.success && list.length > 0){
							that.menuTree = list;
							that.menuParentId = list[0].id;
							that.$nextTick(()=>{
								that.$refs.menuTree.setCurrentKey(list[0].id);
								that.curNode = list[0];
								that.getModel(list[0].id);
							})
						}
					}
				}
				)
		})
	},
	mounted() {
		var tbaleBoxH = this.$refs.tableBox.offsetHeight,
		paginationH = this.$refs.pagination.offsetHeight;
		this.tableH = tbaleBoxH - paginationH;
	},
	methods: {
		expandNode(){

		},
		//节点展开
		nodeExpand(data,node,cur) {
			data.opend = !data.opend;
		},
		//节点闭合
		nodeCollapse(data,node,cur) {
			data.opend = !data.opend;
		},
		changeChecked(){

		},
		handleNodeSideClick(){

		},
		changeCurPage(e){

		},
		//点击节点
		clickNode(data,node,parent){
			this.curNode = data;
			var menuId = data.id;
			//根据菜单id获取模块
			this.getModel(menuId);
		},	
		//获取模块
		getModel(menuId){
			var that = this;
			Parent.ajax(
				'api/services/app/SmModule/GetByMenuID?menuId='+menuId,
				'',
				this,
				(res)=>{
					if(res.success){
						that.tableData = res.result.items;
						that.total = that.tableData.length;
						that.tableDataAll = [];
						for(var i in that.tableData) {
							that.tableData[i].checked = false;
							that.tableDataAll.push(that.tableData[i]);
						}
					}
				},
				"GET"
				)
		},
		//递归判断增加当前数组有没有同名的
		testNameIsRepeat(arr,menuName){
			Parent.boole = false;
			for(var i in arr) {
				//这个数组里面有相同的名称为true
				if(arr[i].menuName == menuName) {
					Parent.boole = true;
					break;
				}
			}
		},
		//递归判断添加菜单同级下有没有重复的名称
		testTreeName(tree,menuName,curNodeId){
			for(var i in tree) {
				//当树的id与选中id相同证明找到同级的数组
				if(tree[i].id == curNodeId) {
					//child的长度大于0才比较
					if(tree[i].child && tree[i].child.length > 0) {
						this.testNameIsRepeat(tree[i].child,menuName);
					} else {
						Parent.boole = false;
					}
				} else {
					if(tree[i].child && tree[i].child.length) {
						this.testTreeName(tree[i].child,menuName,curNodeId);
					}
				}
			}
		},
		//判断修改当前数组有没有同名的
		testReviseNameIsRepeat(arr,menuName,curNodeId){
			Parent.boole = false;
			for(var i in arr) {
				//这个数组里面有相同的名称为true
				if(arr[i].menuName == menuName && arr[i].id != curNodeId) {
					Parent.boole = true;
					break;
				}
			}
		},
		//递归判断修改有没有重复的名称
		testReiveseTreeName(tree,menuName,curNodeId){
			for(var i in tree) {
				//找到当前id的数组 
				if(tree[i].id == curNodeId) {
					this.testReviseNameIsRepeat(tree,menuName,curNodeId);
				} else {
					if(tree[i].child && tree[i].child.length > 0) {
						this.testReiveseTreeName(tree[i].child,menuName,curNodeId);
					}
				}
			}
		},
		//递归修改参数
		reviseMenuTree(obj,id,curObj){
			//obj为修改前的树，修改的节点id，curObj修改之后返回的对象
			for(var i in obj) {
				//id相同的时候把返回的值除了child之外赋值给修改前的
				if(obj[i].id == id) {
					for(var key in obj[i]) {
						if(key != 'child') {
							obj[i][key] = curObj[key];
						}
					}
				} else {
					if(obj[i].child && obj[i].child.length) {
						this.reviseMenuTree(obj[i].child,id,curObj);
					}
				}
			}
			return obj;
		},
		//递归判断删除的节点有没有子节点
		nodeIsChild(tree,curId){
			Parent.boole = false;
			for(var i in tree) {
				//名称相同且id不相同，证明有重复
				if(tree[i].id == curId) {
					if(tree[i].child && tree[i].child.length) {
						Parent.boole = true;
						return;
					}
				} else {
					if(tree[i].child && tree[i].child.length) {
						this.nodeIsChild(tree[i].child,curId);
					}
				}
			}
		},
		//点击添加菜单取消
		menuAddInfoCancel(){
			this.addDialog.addDialog = false;
		},
		//点击添加菜单按钮
		menuClickAddInfo(){
			if(this.tableData.length > 0) {
				this.$message.error(Parent.error.menuNextModel);
				return;
			}
			this.addDialog.addDialog = true;
		},
		//点击增菜单加确认的回调
		addConfirm(res){
			//调用方法true证明有相同的menuName
			this.testTreeName(this.menuTree,this.listLi.menuName.model,this.curNode.id);
			if(Parent.boole){
				this.$message.error(Parent.error.nameRepeat);
				return;
			}
			var obj = {};
			for(var key in res) {
				obj[key] = res[key].model;
			}
			obj.menuParentId = this.curNode.id;
			Parent.ajax(
				'api/services/app/SmMenu/Create',
				obj,
				this,
				(res)=>{
					if(res.success) {
						this.$message({
							message: Parent.success.add,
							type: 'success'
						});
						this.addDialog.addDialog = false;
						for(var key in this.listLi){
							if(this.listLi[key].type == 'dropDown') {
								this.listLi[key].model = 0;
							} else {
								this.listLi[key].model = '';
							}
						}
						this.$refs.menuTree.append(res.result,obj.menuParentId);
					} else {
						this.addDialog.addDialog = true;
					}
				}
				)
		},
		//子组件点击修改菜单
		reviseMenuInfo(){
			if(this.curNode.id == this.menuParentId) {
				this.$message.error(Parent.error.menuRoot);
				return;
			};
			for(var key in this.listLi) {//选中修改的参数赋值给弹窗
				this.listLi[key].model = this.curNode[key];
			}
			var menuType = this.listLi.menuType.model;
			this.listLi.menuType.model = parseInt(menuType);
			this.listLi.menuIsHomePage.model = parseInt(this.listLi.menuIsHomePage.model);
			this.reviseDialog.reviseDialog = true;
		},
		//子组件点击修改菜单确认
		reviseConfirm(listLi){
			//调用方法true证明有相同的menuName
			this.testReiveseTreeName(this.menuTree,this.listLi.menuName.model,this.curNode.id);
			console.log(Parent.boole)
			if(Parent.boole){
				this.$message.error(Parent.error.nameRepeat);
				return;
			}
			var obj = {};
			var that = this;
			for(var key in listLi) {
				obj[key] = listLi[key].model;
			}
			obj.menuParentId = this.curNode.menuParentId;
			obj.id = this.curNode.id;
			Parent.ajax(
				'api/services/app/SmMenu/Update',
				obj,
				this,
				(res)=>{
					if(res.success) {
						//使用递归方法重新更新树赋值
						var menuTree = JSON.parse(JSON.stringify(that.menuTree));
						that.menuTree = that.reviseMenuTree(menuTree,obj.id,res.result);
						that.$message({
							message: Parent.success.revise,
							type: "success"
						});
						this.reviseDialog.reviseDialog = false;
						this.curNode = res.result;
						for(var key in this.listLi){
							if(this.listLi[key].type == 'dropDown') {
								this.listLi[key].model = 0;
							} else {
								this.listLi[key].model = '';
							}
						}
						this.$nextTick(()=>{
							that.$refs.menuTree.setCurrentKey(res.result.id);
						})
					} else {
						this.reviseDialog.reviseDialog = true;
					}
				},
				'put'
				)
		},
		//子组件菜单修改取消
		reviseMenuCancel(){
			this.reviseDialog.reviseDialog = false;
		},
		//点击菜单删除
		clickDelete(){
			var curNodeId = this.curNode.id;
			if(this.menuParentId == curNodeId) {
				this.$message.error(Parent.error.menuRoot);
				return;
			}
			var that = this;
			this.nodeIsChild(this.menuTree,curNodeId);
			console.log(Parent.boole)
			if(!Parent.boole) {
				//表格length为0代表没有模块
				if(this.tableData.length > 0) {
					this.$message.error(Parent.error.menuNextModel);
				} else {
					this.$confirm(Parent.message.delete, Parent.message.title, {
						confirmButtonText: Parent.button.confirm,
						cancelButtonText: Parent.button.cancel,
						type: 'warning'
					}).then(() => {
						Parent.ajax(
							'api/services/app/SmMenu/Delete?id='+curNodeId,
							'',
							that,
							(res)=>{
								if(res.success){
									that.$refs.menuTree.remove(curNodeId);
									that.$message({
										type: 'success',
										message: Parent.success.delete
									});
									//删除成功之后默认选中根节点
									that.curNode = that.menuTree[0];
									that.$refs.menuTree.setCurrentKey(that.curNode.id);
								} else {
									// that.$message.error('删除失败！');
								}
							},
							'DELETE'
							)
					}).catch(() => {

					});
				}
			} else {
				this.$message.error(Parent.error.menuNode);
			}
		},
		//点击模块取消
		modelAddInfoCancel(){
			this.addModelDialog.addDialog = false;
		},
		//子组件点击增加模块按钮
		modelClickAddInfo(){
			//证明是根菜单
			if(this.curNode.id == this.menuParentId) {
				return;
			}
			//判断是不是叶子节点
			this.nodeIsChild(this.menuTree,this.curNode.id);
			if(Parent.boole) {
				this.$message.error(Parent.error.menuNode);
				return;
			}
			this.addModelDialog.addDialog = true;
		},
		//子组件点击增加模块确认
		addModelConfirm(res){
			var obj = {};
			var that = this;
			for(var key in res) {
				obj[key] = res[key].model;
			}
			obj.menuId = this.curNode.id;
			Parent.ajax(
				'api/services/app/SmModule/Create',
				obj,
				this,
				(res)=>{
					if(res.success){
						that.addModelDialog.addDialog = false;
						that.$message({
							message: Parent.success.add,
							type: 'success'
						});
						//当前菜单下所有的模块的数组也要改变
						that.tableDataAll.push(res.result);
						//如果查询没有条件直接添加到表格展示，有值调用查询方法
						if(!that.search.modelName && !that.search.modelNum) {
							that.tableData.push(res.result);
						} else {
							that.query();
						}
						that.total = that.tableData.length;	
					} else {
						that.addModelDialog.addDialog = true;
					}
					
				}
				)
		},
		//子组件点击修改模块
		reviseModelInfo(){
			var num = 0;
			var curModelId;
			for(var i in this.tableData) {
				if(this.tableData[i].checked) {
					this.modelInfo = this.tableData[i];
					curModelId = this.tableData[i].id;
					num++;
				}
			}
			//证明选中一条数据
			if(num == 1) {
				for(var key in this.modelListLi) {//选中修改的参数赋值给弹窗
					this.modelListLi[key].model = this.modelInfo[key];
				}
				this.reviseModelDialog.reviseDialog = true;
			} else {  
				this.$message.error(Parent.error.noData);
			}
		},
		//子组件点击修改模块取消
		reviseModelCancel(){
			this.reviseModelDialog.reviseDialog = false;
		},
		//子组件点击修改模块确定
		reviseModelConfirm(listLi){
			var obj = {};
			var that = this;
			var curModelInfo = this.modelInfo;
			for(var key in listLi) {
				obj[key] = listLi[key].model;
			}
			obj.menuId = this.curNode.id;
			obj.id = curModelInfo.id;
			obj.dataFlag = curModelInfo.dataFlag ? curModelInfo.dataFlag : '';
			console.log(obj);
			Parent.ajax(
				'api/services/app/SmModule/Update',
				obj,
				this,
				(res)=>{
					if(res.success) {
						for(var i in that.tableData) {
							if(that.tableData[i].id == curModelInfo.id) {
								that.tableData.splice(i,1,res.result);
							}
						};
						//当前菜单下所有的模块的数组也要改变
						for(var i in that.tableDataAll) {
							if(curModelInfo.id == that.tableDataAll[i].id) {
								that.tableDataAll.splice(i,1,res.result);
							}
						}
						that.modelInfo = res.result;
						that.reviseModelDialog.reviseDialog = false;
						//恢复弹框的输入框为初始状态
						for(var key in that.modelListLi){
							if(that.modelListLi[key].type == 'dropDown') {
								that.modelListLi[key].model = 0;
							} else {
								that.modelListLi[key].model = '';
							}
						}
						that.$message({
							message: Parent.success.revise,
							type: "success"
						});
					} else {
						that.reviseModelDialog.reviseDialog = true;
					}
				},
				'put'
				)
		},
		//当用户手动勾选数据行的 Checkbox 时触发的事件
		select(selection,row){
			this.modelInfo = row;
			for(var i in this.tableData) {
				if(this.tableData[i].id == row.id) {
					this.tableData[i].checked = !this.tableData[i].checked;
					if(this.tableData[i].checked) {
						this.$refs.table.setCurrentRow(row);
					} else {
						this.$refs.table.setCurrentRow();
					}
					break;
				}
			}
		},
		//点击当前行勾选
		rowClick(row, event, column){
			for(var i in this.tableData) {
				if(this.tableData[i].id == row.id) {
					this.tableData[i].checked = !this.tableData[i].checked;
					this.$refs.table.toggleRowSelection(row,this.tableData[i].checked);
					if(this.tableData[i].checked) {
						this.$refs.table.setCurrentRow(row);
					} else {
						this.$refs.table.setCurrentRow();
					}
					break;
				}
			}
		},
		//当用户全部勾选
		selectAll(selection){
			if(selection.length > 0) {
				for(var i in this.tableData) {
					this.tableData[i].checked = true;
				}
			} else {
				for(var i in this.tableData) {
					this.tableData[i].checked = false;
				}
			}
		},
		//点击删除模块
		clickModelDelete(){
			var that = this;
			var curModelId = [];
			for(var i in this.tableData) {
				if(this.tableData[i].checked) {
					curModelId.push(this.tableData[i].id);
				}
			}
			if(curModelId.length > 0) {
				this.$confirm(Parent.message.delete, Parent.message.title, {
					confirmButtonText: Parent.button.confirm,
					cancelButtonText: Parent.button.cancel,
					type: 'warning'
				}).then(() => {
					Parent.ajax(
						'api/services/app/SmModule/DeleteMore?input=' + curModelId.join(','),
						'',
						this,
						(res)=>{
							if(res.success) {
								for(var i = 0;i < curModelId.length;i++) {
									//当前菜单下所有的模块的数组也要改变
									for(var j = 0;j < that.tableDataAll.length;j++) {
										if(curModelId[i] == that.tableDataAll[j].id) {
											that.tableDataAll.splice(j,1);
											j--;
										}
									};
									//表格展示的数据
									for(var j = 0;j < that.tableData.length;j++) {
										if(curModelId[i] == that.tableData[j].id) {
											that.tableData.splice(j,1);
											j--;
										}
									};
								}
								that.modelInfo = {};
								that.total = that.tableData.length;
								that.$message({
									type: "success",
									message: Parent.success.delete
								});
							};
						},
						"DELETE"
						)
				}).catch(() => {

				});
			} else {
				this.$message.error(Parent.error.noData);
			}
		},
		//点击查询的回调
		query(){
			var modelNum = this.search.modelNum,
			modelName = this.search.modelName;
			if(modelNum || modelName) {
				//this.tableDataAll是当前菜单的所以模块
				this.tableData = JSON.parse(JSON.stringify(this.tableDataAll));
				for(var i = 0;i < this.tableData.length;i++) {
					//根据indexOf来判断，这里是两个需要比较的参数
					if(modelNum && modelName) {
						if(this.tableData[i].moduleName.indexOf(modelName) == -1 || this.tableData[i].moduleUniqeCode.indexOf(modelNum) == -1) {
							this.tableData.splice(i,1);
							i--;
						}
					} else if(modelNum && !modelName) {
						if(this.tableData[i].moduleUniqeCode.indexOf(modelNum) == -1) {
							this.tableData.splice(i,1);
							i--;
						} 
					}else if(!modelNum && modelName) {
						console.log(this.tableData[i].moduleName.indexOf(modelName))
						if(this.tableData[i].moduleName.indexOf(modelName) == -1) {
							this.tableData.splice(i,1);
							i--;
						} 	
					}
				}
			} else {
				//为空的时候全部展示出来
				this.tableData = JSON.parse(JSON.stringify(this.tableDataAll));
			}
		}
	},
	store,
	watch:{

	}
}
</script>

<style scoped>
.container{flex: 1;padding: 0 0 0 5px;background:#e7e7eb;display:flex;overflow:hidden;border: none;}
.tree-aside{width: 250px;display: flex;flex-direction: column;background: #fff;}
.row-between{justify-content: space-between;}
.row-center{justify-content: center;}
.row-PL{padding-left: 10px;}
.current{background: #cad5e1;height: 40px;line-height: 40px;font-size: 16px;color: #fff;padding-left: 15px;margin-bottom: 10px;}
.el-aside{flex: 1;margin-top: 10px;}
.content{flex: 1;margin: 0 0 0 5px;background: #fff;display: flex;flex-direction: column;}
.table-box{padding: 10px 5px 0;flex: 1;display: flex;flex-direction: column;}
.block{display: flex;justify-content: center;padding: 40px 0 20px;}
</style>


